.timeline {
  position: relative;
  display: flex;
}

.timeline:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #D54100;
}

.timeline-pointer {
  display: flex;
  justify-content: center;
  position: relative;
  font-weight: 500;
}

.timeline-pointer:before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  transform: translateY(-33%);
  background-color: #D54100;
}

.timeline p {
